
import { defineComponent, ref } from 'vue';
import Toggle from '@vueform/toggle';
import { QuickFetch, QuickData } from '@/quick';
import SmallModal from '@/components/SmallModal.vue';
import { useRoute } from 'vue-router';
import { QuillEditor } from '@vueup/vue-quill';
import '@vueup/vue-quill/dist/vue-quill.snow.css';

export default defineComponent({
  props: ['gameType'],
  setup(props, context) {
    // quick
    const qf = new QuickFetch();
    const qd = new QuickData();
    const route = useRoute();
    // settings
    const gameCode: any = ref(props.gameType);
    const displayModal = ref(0);
    // lists
    const sportList: any = ref([]);
    function getSport() {
      qf.Url('sports/list').GetAll(sportList);
    }
    getSport();
    const divisionList: any = ref([]);
    function getDivision() {
      qf.Url('division/list').GetAll(divisionList);
    }
    getDivision();
    // data
    const nameCh = ref('');
    const nameEn = ref('');
    const info = ref('');
    const type = ref('athl');
    const hostDivision: any = ref([]);
    const regStart = ref('');
    const regEnd = ref('');
    const eventStart = ref('');
    const nsysu = ref('0');
    const regSwitch = ref('1');
    const sameOrg = ref('0');
    const sameDept = ref('0');
    const onlyTeam = ref('0');
    const regUnit = ref('0');
    const useReg = ref('1');
    const regUrl = ref('');
    const useManage = ref('1');
    const manageUrl = ref('');
    const useSite = ref('1');
    const siteUrl = ref('');
    function getData() {
      qf.Url(`games/${route.params.gameId}`).Get().then((res: any) => {
        nameCh.value = res.name_ch;
        nameEn.value = res.name_en;
        info.value = res.info;
        type.value = res.type;
        hostDivision.value = JSON.parse(res.host_division);
        regStart.value = res.reg_start;
        regEnd.value = res.reg_end;
        eventStart.value = res.event_start;
        nsysu.value = String(res.nsysu);
        regSwitch.value = String(res.reg_switch);
        sameOrg.value = String(res.same_org);
        sameDept.value = String(res.same_dept);
        onlyTeam.value = String(res.only_team);
        regUnit.value = String(res.reg_unit);
        useReg.value = String(res.use_reg);
        regUrl.value = res.reg_url;
        useManage.value = String(res.use_manage);
        manageUrl.value = res.manage_url;
        useSite.value = String(res.use_site);
        siteUrl.value = res.site_url;
      });
    }
    getData();
    // submit
    function submitAll() {
      if (useReg.value === '1') {
        regUrl.value = '';
      }
      if (useManage.value === '1') {
        manageUrl.value = '';
      }
      if (useSite.value === '1') {
        siteUrl.value = '';
      }
      qf.Dataset()
        .AddObj('name_ch', nameCh)
        .AddObj('name_en', nameEn)
        .AddObj('info', info)
        .AddObj('type', type)
        .AddObj('reg_start', regStart)
        .AddObj('reg_end', regEnd)
        .AddObj('event_start', eventStart)
        .AddObj('nsysu', nsysu)
        .AddObj('reg_switch', regSwitch)
        .Add('host_division', JSON.stringify(hostDivision.value))
        .AddObj('same_org', sameOrg)
        .AddObj('same_dept', sameDept)
        .AddObj('only_team', onlyTeam)
        .AddObj('reg_unit', regUnit)
        .AddObj('use_reg', useReg)
        .AddObj('reg_url', regUrl)
        .AddObj('use_manage', useManage)
        .AddObj('manage_url', manageUrl)
        .AddObj('use_site', useSite)
        .AddObj('site_url', siteUrl);
      if (new Blob([JSON.stringify(qf.GetDset())]).size > 10000000) {
        qd.Alert('總大小大於10Mb，無法上傳');
        return;
      }
      qf.Url('games/edit').Patch(String(route.params.gameId), qf.GetDset()).then((res: any) => {
        if (res.message === 'done') {
          qd.Alert('已更新比賽資訊');
          context.emit('refresh');
          context.emit('close_modal');
        }
      });
    }
    return {
      nameCh,
      nameEn,
      info,
      type,
      hostDivision,
      regStart,
      regEnd,
      eventStart,
      nsysu,
      regSwitch,
      sameOrg,
      sameDept,
      onlyTeam,
      regUnit,
      useReg,
      regUrl,
      useManage,
      manageUrl,
      useSite,
      siteUrl,
      submitAll,
      sportList,
      divisionList,
      displayModal,
      gameCode,
      isEdit: ref(false),
    };
  },
  components: {
    Toggle,
    SmallModal,
    QuillEditor,
  },
});
